.signuppopupbox {
  background-color: rgba(0, 0, 0, 0.701);
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  flex-direction: column;
  color: red;
  top: 0;
}

.popupbox {
  background-color: black;
  width: 500px;
  height: 500px;
  display: block;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  color: rgb(252, 252, 252);
  padding: 0px 100px;
}
.form {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 10px;
  gap: 40px;
  font-family: Arial, Helvetica, sans-serif;
}
.form h2 {
  text-align: center;
  font-size: 22px;
  font-weight: 800;
}
.inputbox1 {
  display: flex;
  flex-direction: column;
  gap: 20px;
  justify-content: center;
  align-items: center;
}
.inputbox1 input {
  height: 40px;
  font-size: 18px;
  padding-left: 10px;
  background-color: transparent;
  border: 2px solid gray;
  border-radius: 2px;
  color: white;
  letter-spacing: 0.2mm;
  width: 100%;
  /* max-width: 400px; */
}
.inputbox1 button {
  width: 40%;
  padding: 10px;
  background-color: red;
  border: none;
  border-radius: 5px;
  color: white;
  font-size: 18px;
  font-weight: 600;
  font-family: Arial, Helvetica, sans-serif;
}
.checkboxpera {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
  text-align: left;
  width: 100%;
  padding: 0px 10px;
}
.checkboxpera input {
  width: 20px;
}
.sign-up-pera p {
  padding: 10px;
}
.sign-up-pera p span {
  color: orange;
  font-size: 18px;
  font-weight: 600;
}

.sign-up-pera p .learn {
  color: blue;
  font-weight: 400;
}

.signupafterbtn:hover {
  cursor: pointer;
  color: green;
}

.btnover:hover {
  background-color: rgb(252, 50, 50);
  border: 1px solid rgb(131, 131, 131);
}

#signup {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  top: 12vh;

  position: absolute;
}
#signupp {
  display: flex;
  justify-content: center;
  align-items: center;
  left: 35%;
  position: relative;
}
