.moviepage {
  background-color: rgb(0, 0, 0);
  color: white;
  padding: 4px 10px;
}
.moviepage h1 {
  padding: 0px 50px;
  font-size: 39px;
}
.movie-list {
  /* border: 2px solid red; */
  background-color: black;
  color: wheat;
  padding: 30px 30px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 20px;
  position: relative;
}
.movie-grid-boxes {
  width: 100%;
  overflow: hidden;
  border: 2px solid rgb(107, 107, 107);
  background-color: rgba(41, 41, 41, 0.826);
}
.movie-boxes {
  width: 100%;
  height: 230px;
  /* border: 2px solid gray; */
  padding: 1px;
}
.movie-boxes img {
  width: 100%;
  height: 230px;
  /* border: 2px solid gray; */
  padding: 10px;
}
.movie-boxes img:hover {
  width: 100%;
  height: 240px;
  padding: 1px;
  cursor: pointer;
}
.movie-grid-boxes{
  cursor: pointer;
}
.movie-name {
  color: white;
  font-size: 16px;
  text-align: center;
  padding: 2px 10px;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-weight: 400;
}
.movie-year {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  padding: 8px 10px 2px;
  gap: 4px;
  text-align: left;
}
.popularity {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  padding: 8px 10px;
  gap: 4px;
  text-align: left;
  color: gold;
  /* height: 10px; */
}

.movie-year > span {
  color: rgb(255, 255, 255);
  font-size: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.popularity > span {
  color: white;
  font-size: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* secondsection */
.secondsection {
  background-color: rgb(0, 0, 0);
  width: 100%;
  height:80vh;
  color: white;
  border-top: 1px solid rgba(100, 99, 99, 0.311);
  display: flex;
  flex-direction: column;
  padding: 10px 30px;
}
.secondsection h1 {
  padding: 0px 50px;
  font-size: 39px;
}
.movie-listtwo {
  display: flex;
  /* border: 2px solid red; */
  padding: 20px;
  width: 100%;
  /* height: 450px; */
}
.movie-posters {
  border: 2px solid rgba(153, 153, 153, 0.61);
  padding: 20px 30px 20px;
  margin: 10px;
  text-align: center;
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
  background-color: rgba(41, 41, 41, 0.826);

}
.movie-posters img {
  border: 2px solid gray;
}


/* searchbox-container  searchbox-container  searchbox-container  searchbox-container */

.searchbox-container{
  display: flex;
  height: 25vh;
  background-color: black;
  color: white;
  justify-content: center;
  align-items: center;
}

.moviesearch-box{
  display: flex;
  justify-content: center;
  align-items: center;
}
.moviesearch-box input{
  height: 45px;
  width: 380px;
  border-radius: 5px 0px 0px 5px;
  outline: none;
  border: 1px solid rgb(195, 194, 194);
  border-right:none;
  padding:0px 10px;
  font-size: 17px;

}
.moviesearch-box button{
  height: 45px;
  width: 60px;
  background-color: red;
  border: 1px solid rgba(207, 207, 207, 0.904);
  border-radius: 0px 5px 5px 0px;

  border-left: none;
  outline: none;
  
}
.search-icons{
  font-size: 25px;
  color: wheat;
}

/* loardmore */

.load-more-container{
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
padding: 30px 0px;
}
.loardmore{
width: 250px;
height: 40px;
background-color: red;
font-size: 20px;
border: 1px solid white;
border-radius: 5px;
font-weight: bold;
color: white;
}
.loardmore:hover{
  color: black;
  background-color: yellow;
  transition-duration: 0.4s;
}






/* buttonsection buttonsection buttonsection buttonsection buttonsection vbuttonsection */


.buttonsection{
  background-color: beige;
  height: 10vh;
  width: 100%;

}









/* movie-model  movie-model  movie-model   movie-model   movie-model*/

.movie-model{
 width: 100%;
 height: 100vh;
 background-color: rgba(39, 38, 38, 0.211);
 position: absolute;
 display: flex;
 justify-content: center;
 align-items: center;

}

.movie-poster{
  background-color: rgb(59, 58, 58);
  width: 650px;
  height: 450px;
  display: flex;
  flex-direction: row ;
  padding: 20px;
  justify-content: center;
  align-items: center;
  gap: 15px;
  position: relative;
  /* padding: ; */
}
.movie-poster img{
  width: 60%;
  /* height: 100%; */
}

.close-movie-poster{
  position: absolute;
  top: 12px;
  right: 30px;
}
.movie-details{
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 12px;
  color: rgb(190, 189, 189);
}
.movie-details-pera{
  display: flex;
  width: 100%;
  justify-content: flex-start;
  gap: 30px;
  align-items: center;
  color: rgb(204, 229, 250);
}
.movie-details-pera span{
  display: flex;
  justify-content: center ;
  align-items: center;
  gap: 2px;
}